import React, { useState } from "react";
import { onDeleteWall } from "./utils";
import { Accordion, OverlayTrigger, Tooltip } from "react-bootstrap";
import AccordionItemSection from "./AccordionItemSection";
import RevokeModal from "./RevokeModal";
import NetworkIconsSection from "./NetworkIconsSection";
import { TOAST_SUCCESS } from "../../../../utils";
import HttpClient from "../../../../actions/http-client";
import { DELETE_EXTRA_FEED, DELETE_EXTRA_FEED_WALL } from "../../../../actions/api";
import { shallowEqual, useSelector } from "react-redux";


function WallFeed({ feedsList, deleteWall, deleteFeedFromWall, extraGallery, extraFeeds }) {
  const [activePanelIndex, setActivePanelIndex] = useState(0);
  const { UserRule } = useSelector(state => state.authorized?.userData.user_data, shallowEqual);
  const { allfeedsCount } = useSelector(state => state.dashboardData?.dashboarddata, shallowEqual);


  const deleteExtraFeed = (wallId, feedsList) => {
    new HttpClient().post(DELETE_EXTRA_FEED, { "feedId": feedsList, "isCollaborator": 0 }).then((response) => {
      if (response.data && Object.keys(response.data).length > 0) {
        const { message } = response.data;	
        TOAST_SUCCESS(message);
        deleteFeedFromWall(wallId, feedsList);
      }
    }).catch((ex) => {

    });
  }

  const deleteExtraFeedWall = (wallID) => {
    new HttpClient().post(DELETE_EXTRA_FEED_WALL, { "wallId": `${wallID}`, "isCollaborator": 0 }).then((response) => {
      if (response.data && Object.keys(response.data).length > 0) {
        const { response_message } = response.data;
        TOAST_SUCCESS(response_message);
        setTimeout(() => {
          deleteWall(wallID);
        }, 500);
      }
    }).catch((ex) => {

    });
  }

  const getTotalExtraFeeds = (list) => {
    return list.reduce((acc, curr) => {
      if (curr.feed.length > 0) {
        acc += curr.feed.length;
      }
      return acc;
    }, 0);
  }

  return (
    <div id="plan_restrict_new" className="plan_restrict_body">
      <div className="card card-custom card-stretch gutter-b">
        <div className="card-body">
          <div className="card-header mb-3 border-0">
            <div className="row">
              <div className="col-sm-6 col-5">
                <h6 className="font-size-lg mb-0 text-nowrap">Gallery</h6>
              </div>
              <div className="col-sm-2 col-1">
                <h6 className="text-center font-size-lg mb-0 text-nowrap">Feeds</h6>
              </div>
              <div className="col-sm-2 col-5">
                <h6 className="text-center font-size-lg mb-0 text-nowrap">Network</h6>
              </div>
              <div className="col-sm-2 col-1">
                <h6 className="text-right font-size-lg mb-0 text-nowrap">Action</h6>
              </div>
            </div>
          </div>
          <div id="planAccordion">
            <Accordion defaultActiveKey="0" onChange={(uuid) => setActivePanelIndex(uuid[0])}>
              {feedsList.map((feed, i) => {
                const isExpanded = activePanelIndex === i;
                const networkIconList = feed && feed.feed.length > 0 ? feed.feed.map((network) => network.Networkicon) : [];
                return (
                  <Accordion.Item eventKey={feed.wallId} key={feed.wallId} className="mb-3 border-1 rounded-2 overflow-hidden">
                    <Accordion.Header>
                      <div className="container-fluid">
                        <div className="row ps-3">
                          <div className="col-sm-6 col-5">
                            <h6 className="font-size-lg my-2">{feed.wallname}</h6>
                          </div>
                          <div className="col-sm-2 col-1">
                            <h6 className="text-center my-2">{feed.feed.length}</h6>
                          </div>
                          <div className="col-sm-2 col-5">
                            {networkIconList.length > 0 ? <NetworkIconsSection networkList={networkIconList} /> : <p className="mb-0 text-center">-</p>}
                          </div>
                          <div className="col-sm-2 col-1 text-right">
                            <button type="button" onClick={() => onDeleteWall(() => deleteExtraFeedWall(feed.wallId))} className="btn btn-icon btn-sm btn-clean">
                              <span className="svg-icon svg-icon-md svg-icon-dark-75 me-0">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                  <g data-name="Group 4331" transform="translate(10956 -4533)">
                                    <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                    <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                      <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd"></path>
                                    </g>
                                  </g>
                                </svg>
                              </span>
                            </button>
                          </div>
                        </div>
                      </div>
                    </Accordion.Header>
                    <Accordion.Body className="planfeed-list-body p-0">
                      {feed.feed.length > 0 &&
                        <AccordionItemSection feeds={feed.feed} wallId={feed.wallId} deleteExtraFeed={deleteExtraFeed} />
                      }
                    </Accordion.Body>
                  </Accordion.Item>
                );
              })}
            </Accordion>
            <RevokeModal />
            <div className="mt-8">
              <p className="plan_restrict_count text-center text-red mb-0">
                You have exceeded your plan's limit. Delete extra{" "}
                <strong>
                {extraGallery ? `Gallery (${extraGallery})` : ''}	
                {' '}	
                {extraFeeds ? `Feeds (${extraFeeds})` : ''}
                {/* Gallery (<span className="totalWidget">{feedsList.length > 0 ? getTotalExtraFeeds(feedsList) : 0}</span>) */}
                </strong>{" "}
                for a seamless experience.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default WallFeed;