import React from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";


const NetworkIconsSection = ({ networkList }) => {
    return <h6 className="text-center mb-0 social-network p_tooltip d-flex justify-content-center">
        <div className="symbol-group symbol-hover">
            {
                networkList.length >= 3 ? <>
                    <div className="symbol symbol-30 symbol-circle" data-toggle="tooltip" title="">
                        <span className="symbol-label"><i className={`fab ${networkList[0]}`} /></span>
                    </div>
                    <div className="symbol symbol-30 symbol-circle" data-toggle="tooltip" title="">
                        <span className="symbol-label"><i className={`fab ${networkList[1]}`} /></span>
                    </div>
                    <OverlayTrigger
                        placement='top'
                        overlay={
                            <Tooltip className="light">
                                <div className="tooltip_symbol d-flex flex-wrap">
                                    {
                                        networkList.slice(2, networkList.length).map((networkIcon) => (<div className="symbol symbol-30 symbol-circle" data-toggle="tooltip" title="">
                                                <span className="symbol-label"><i className={`fab ${networkIcon}`} /></span>
                                            </div>
                                        ))
                                    }
                                </div>
                            </Tooltip>
                        }
                    >
                        <div className="symbol symbol-30 symbol-circle symbol-light">
                            <span className="symbol-label font-weight-bold text-dark font-size-sm">{`${networkList.length - 2}+`}</span>
                        </div>
                    </OverlayTrigger>
                </> :
                <>
                    {
                        networkList.map((networkIcon) =>(<div className="symbol symbol-30 symbol-circle symbol-light">
                        <span className="symbol-label font-weight-bold text-dark font-size-sm"><i className={`fab ${networkIcon}`} /></span>
                    </div>))
                    }
                    </>
            }
        </div>
    </h6>
}

export default NetworkIconsSection;