import React, { useEffect, useState } from 'react'
import WallFeed from './Components/WallFeed'
import HttpClient from '../../../actions/http-client';
import { GET_EXTRA_FEEDS_API, HOST_URL } from '../../../actions/api';
import Logo from '../sideMenuBar/logo';
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import { getUserInfo } from '../../../actions/loginActions';
import { useNavigate } from 'react-router-dom';

function WallLimitExceed() {
    const [feeds, setFeeds] = useState([]);
    const [extraGallery, setExtraGallery] = useState(0)	
    const [extraFeeds, setExtraFeeds] = useState(0)
  const { UserRule } = useSelector(state => state.authorized?.userData.user_data, shallowEqual);
    const dispatch = useDispatch()
    const navigation = useNavigate()
    useEffect(() => {
        getFeeds();
    }, [])


    const getFeeds = () => {
        new HttpClient().post(GET_EXTRA_FEEDS_API).then((response) => {
            if (response.data && Object.keys(response.data).length > 0) {
                const { responseData } = response.data;
                if (responseData && Object.keys(responseData).length > 0) {
                    const { wallList, delete_extra_feed, delete_extra_gallery } = responseData;	
                    setExtraGallery(delete_extra_gallery)	
                    setExtraFeeds(delete_extra_feed)
                    if (wallList.length > 0) {
                        setFeeds(wallList);
                    }
                }
            }
        }).catch((ex) => {

        });
    }

    const deleteWall = (wallId) => {
        const updateFeeds = feeds.filter(feed => feed.wallId != wallId);
        setFeeds(updateFeeds);
        getFeeds()
        dispatch(getUserInfo(() => {}))
        if(parseInt(UserRule.feeds) <= updateFeeds.length){
            // localStorage.removeItem('wallLimitExceed')
            navigation('/walls/home')
        }

    }

    const deleteFeedFromWall = (wallId, feedIdList) => {
        const toDeleteFeeds = feedIdList.map(feedId => Number(feedId));
        const updateWallFeeds = feeds.map(wall => {
            if (wall.wallId === wallId) {
                const updatedFeedList = wall.feed.filter((item) => toDeleteFeeds.indexOf(item.id) === -1);
                wall.feed = updatedFeedList;
            }
            return wall;
        });
        setFeeds(updateWallFeeds);
        getFeeds()
        dispatch(getUserInfo(() => {}))
        if(parseInt(UserRule.feeds) <= updateWallFeeds.length){
            // localStorage.removeItem('wallLimitExceed')
            navigation('/walls/home')
        }

    }



    return (
        <div className="home_screen_warp">
            <div className="d-flex flex-column-fluid main_content no_subheader align-items-center flex-position-center justify-content-center">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="screen_wrapper">
                                <div className="after_intro_screen">
                                    <div className="row">
                                        <div className="col-sm-12 text-center">
                                            <div className="d-flex align-items-center justify-content-center mb-10 mb-lg-15">
                                                <Logo />
                                            </div>
                                        </div>
                                    </div>
                                    <WallFeed feedsList={feeds} extraFeeds={extraFeeds} extraGallery={extraGallery} deleteWall={deleteWall} deleteFeedFromWall={deleteFeedFromWall} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default WallLimitExceed