import { onDeleteWall } from "./utils";
import ReactHtmlParser from 'react-html-parser';

export default function AccordionItemSection({ feeds, wallId, deleteExtraFeed, }) {

  const getAddFeedsId = (feedsList) => {
    if (feedsList && feedsList.length > 0) {
      return feedsList.map((feed) => `${feed.id}`)
    }
  }

  const getfeedTag=(tag)=>{
   if(tag){
    if(tag.includes('<i class')){
      return <>{ReactHtmlParser(tag)}{" "}</>
       }
       return tag;
   }else return '';
  }

  return (
      <div className="card mb-0">
          <div className="card-body p-0">
            <div className="planfeed-list-head">
              <div className="row">
                <div className="col-sm-8 col-8">
                  <p className="mb-0">Feeds</p>
                </div>
                <div className="col-sm-4 col-4">
                  <p className="text-right mb-0 ">
                    <button type="button" onClick={() => onDeleteWall(() => deleteExtraFeed(wallId, getAddFeedsId(feeds))) } wallid={wallId} className="btn p-0 d-flex align-items-end justify-content-end w-100">
                      <span className="mr-2 font-size-sm text-nowrap">Delete All</span>
                      <span className="svg-icon svg-icon-md svg-icon-dark-75">
                          <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                            <g data-name="Group 4331" transform="translate(10956 -4533)">
                              <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                              <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd"></path>
                              </g>
                            </g>
                          </svg>
                      </span>
                    </button>
                  </p>
                </div>
              </div>
            </div>
            <div className="planfeed-list-body">
              {feeds.length > 0
                ? feeds.map((feed) => (
                  <div className={`planfeed-list-row feedarea-${feed.id}`} key={feed.id}>
                    <div className="container-fluid">
                      <div className="row">
                        <div className="col-sm-6 col-8">
                          <p className="mb-0 widget-name">
                            <a className="d-flex align-items-center">
                              <div className="symbol symbol-30 symbol-circle icon-shadow" data-toggle="tooltip" title={feed.Networkdescription}>
                                <span className="symbol-label bg-white font-size-lg"><i title={feed.Networkdescription} className={`fab ${feed.Networkicon}`} key={feed.Networkid}></i></span>
                              </div>
                              <span className="ml-3">
                                {getfeedTag(feed.feedsFilterprefix)}
                                {feed.name}
                              </span>
                            </a>
                          </p>
                        </div>
                        <div className="col-sm-2 col-3">
                          <span className="text-center d-flex justify-content-center">
                            <span className="d-none d-sm-flex me-1">Total</span> Post: {feed.totalrecords}
                          </span>
                        </div>
                        <div className="col-sm-4 col-1">
                          <p className="text-right mb-0">
                            <button type="button" onClick={() => onDeleteWall(() => deleteExtraFeed(wallId, [`${feed.id}`]))}
                              className="btn btn-link btn-icon btn-sm btn-clean">
                              <span className="svg-icon svg-icon-md svg-icon-dark-75">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                                  <g data-name="Group 4331" transform="translate(10956 -4533)">
                                    <rect id="Rectangle_2069" data-name="Rectangle 2069" width="24" height="24" transform="translate(-10956 4533)" fill="none" opacity="0"></rect>
                                    <g id="Group_4324" data-name="Group 4324" transform="translate(-10952 4536)">
                                      <path id="Path_5188" data-name="Path 5188" d="M2.722,16.328A2,2,0,0,0,4.694,18h6.612a2,2,0,0,0,1.972-1.672L14,6H2ZM9,9.5A.5.5,0,0,1,9.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5Zm-4,0A.5.5,0,0,1,5.5,9h1a.5.5,0,0,1,.5.5v5a.5.5,0,0,1-.5.5h-1a.5.5,0,0,1-.5-.5ZM15,2H11A2,2,0,0,0,9,0H7A2,2,0,0,0,5,2H1A1,1,0,0,0,1,4H15a1,1,0,0,0,0-2Z" fill="#545454" fillRule="evenodd"></path>
                                    </g>
                                  </g>
                                </svg>
                            </span>
                            </button>
                          </p>
                        </div>
                      </div>
                    </div>
                  </div>
                ))
                : null}
            </div>
          </div>
          </div>
  );
}